// 一段时间转化为时分秒
export function momentFormatter (moment) {
  if (moment / 3600000 > 1) {
    return `${Math.floor(moment / 3600000)}h`
  }
  if (moment / 60000 > 1) {
    return `${Math.floor(moment / 60000)}min`
  }
  if (moment / 1000 > 1) {
    return `${Math.floor(moment / 1000)}s`
  }
  return `${moment}ms`
}

// 时刻转化为当天零点
export function startOfDay (time) {
  if (!time) { time = Date.now() }
  return new Date(new Date(time).toLocaleDateString()).getTime()
}

export function startOfPreDay (time) {
  return startOfDay(time) - 3600 * 24 * 1000
}



