<template>
  <div>
    <RouteImage
      to=""
      imgUrl="static/icon/earlyBed.png"
      name="早睡签到"
      @click="handleClick"></RouteImage>
  </div>
</template>

<script>
  import Vue from 'vue'
  import {Component} from 'vue-property-decorator'
  import { Action } from 'vuex-class'
  import RouteImage from '../../components/routeImage/Index.vue'
  import { getEarlyBedConfig, earlyBedSignon } from '@/api/signon'
  import { MessageBox } from 'mint-ui'
  import { startOfDay, startOfPreDay } from '@/utils/dateFormatter'

  @Component({
    components: {
      RouteImage
    }
  })
  export default class index extends Vue {
    @Action('updateGold') updateGold

    handleClick () {
      MessageBox({
        title: '提示',
        message: `确定签到？`,
        showCancelButton: true
      }).then(action => {
        if (action === 'confirm') {
          let now = new Date()
          let nowHour = now.getHours()
          // 判断签到时间
          if (nowHour < 21 || nowHour > 23) {
            MessageBox('提示', '晚上9点到11点才能签到！')
            return
          }
          let config = getEarlyBedConfig()
          if (config.timestamp !== 0) {
            if (startOfDay(config.timestamp) === startOfDay()) {
              MessageBox('提示', `今天已签到，签到天数：${config.num}`)
              return
            }
            // 判断昨天是否签到,没签到清空签到天数
            if (startOfDay(config.timestamp) !== startOfPreDay()) {
              config.num = 0
            }
          }
          config.timestamp = Date.now()
          config.num++
          let msg = `签到成功，签到天数：${config.num}`
          // 计算灵石奖励
          let base = Math.floor(config.num / 7)
          let gold = base
          if (nowHour < 10) {
            gold = base + 2
          } else {
            gold = base + 1
          }
          // 第七天额外奖励
          if (config.num % 7 === 0) {
            gold += base * 50
            msg += `，七天额外奖励灵石${base * 50}，共获得灵石${gold}`
          } else {
            msg += `，共获得灵石${gold}`
          }
          this.updateGold(gold)
          earlyBedSignon(config)
          MessageBox('提示', msg)
        }
      })
    }
  }
</script>

<style scoped></style>


