export function getEarlyBedConfig () {
  let earlyBed = window.localStorage.getItem('earlybed')
  if (!earlyBed) {
    earlyBed = {
      timestamp: 0,
      num: 0
    }
  } else {
    earlyBed = JSON.parse(earlyBed)
  }
  return earlyBed
}
// 早睡签到
export function earlyBedSignon (config) {
  window.localStorage.setItem('earlybed', JSON.stringify(config))
}


