<template>
  <router-link :to="to">
    <div class="other-app-item" @click="$emit('click')">
      <div>
        <img class="other-apps-img" :src="imgUrl">
      </div>
      <div>{{name}}</div>
    </div>
  </router-link>
</template>

<script>
  import Vue from 'vue'
  import {Component, Prop} from 'vue-property-decorator'

  @Component
  export default class Index extends Vue {
    @Prop(String) to
    @Prop(String) imgUrl
    @Prop(String) name
  }
</script>

<style scoped>
  .other-app-item {
    display: inline-block;
    width: 100px;
    text-align: center;
    margin: 20px;
  }

  .other-apps-img {
    width: 80px;
    height: 80px;
  }
</style>


