import talentConfig from '../../views/talent/talentConfig'

const getters = {
  getUserInfo: state => {
    return state.userInfo
  },
  getUserZdl: state => {
    let talents = state.userInfo.talents
    if (!talents) return 0
    let zdl = 0;
    let tsConfig = talentConfig.ts
    for (let i = 0; i < tsConfig.length; ++i) {
      let ts = tsConfig[i]
      if (talents[ts.id]) {
        zdl += ts.items[talents[ts.id].level].zdl
      }
    }
    return zdl
  }
};

export default getters



